// src/components/Greeting.js
import React, { useState, useEffect } from 'react';

const Greeting = React.memo(({ userName: _userName }) => {
  const [greetingText, setGreetingText] = useState('');
  const [greetingEmoji, setGreetingEmoji] = useState('');

  useEffect(() => {
    const updateGreeting = () => {
      const hour = new Date().getHours();
      let text = '';
      let emoji = '';

      if (hour >= 5 && hour < 12) {
        text = 'Good morning';
        emoji = 'â˜€ï¸';
      } else if (hour >= 12 && hour < 17) {
        text = 'Good afternoon';
        emoji = 'ðŸ‘‹';
      } else if (hour >= 17 && hour < 22) {
        text = 'Good evening';
        emoji = 'ðŸŒ™';
      } else {
        text = 'Time to sleep';
        emoji = 'ðŸ˜´';
      }
      setGreetingText(text);
      setGreetingEmoji(emoji);
    };

    updateGreeting();
    const intervalId = setInterval(updateGreeting, 60 * 1000);
    return () => clearInterval(intervalId);
  }, []);

  return (
    <div>
      <p className="text-base font-light text-gray-500 flex items-center">
        {greetingText}
        <span className="ml-1 text-base">{greetingEmoji}</span>
      </p>
    </div>
  );
});

export default Greeting;
