// src/components/FocusItemCard.js
import React, { useState, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useHandlers } from '../context/HandlerContext';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';

const FocusItemCard = React.memo(({ item, onRemove, onToggle }) => {
  const handlers = useHandlers();
  const { attributes, listeners, setNodeRef, transform, transition, isDragging } = useSortable({
    id: item.id,
  });

  const [isRemoving, setIsRemoving] = useState(false);

  const style = {
    transform: CSS.Transform.toString(transform),
    transition: transition || 'transform 0.2s cubic-bezier(0.22, 1, 0.36, 1)',
    willChange: 'transform',
    opacity: isDragging ? 0.7 : 1,
    background: isDragging ? '#f0f0f0' : undefined,
  };

  const handleCheckboxClick = useCallback((e) => {
    e.stopPropagation();
    onToggle?.(item.subject, item.title, !item.completed) || 
    handlers.toggleTestCompletion(item.subject, item.title, !item.completed);
  }, [item.subject, item.title, item.completed, onToggle, handlers]);

  const [isRemovingItem, setIsRemovingItem] = useState(false);

  const handleRemoveClick = useCallback(async (e) => {
    e.stopPropagation();
    e.preventDefault();
    
    if (isRemovingItem) return; // Prevent multiple clicks
    
    setIsRemovingItem(true);
    setIsRemoving(true);
    
    try {
      // Let the animation start
      await new Promise(resolve => setTimeout(resolve, 100));
      
      // Then trigger the removal
      await (onRemove?.(item) || handlers.removeFocusItem(item));
    } catch (error) {
      console.error('Error removing item:', error);
      setIsRemoving(false);
      setIsRemovingItem(false);
    }
  }, [item, onRemove, handlers, isRemovingItem]);

  // Card classes
  const cardClasses = `
    flex items-center w-full p-4 rounded-lg border-l-4 transition-all duration-200 ease-in-out
    ${item.completed ? 'bg-green-100' : 'bg-gray-50'} hover:shadow-lg cursor-pointer
  `;

  const titleClasses = `
    font-bold text-gray-800 text-md
    ${item.completed ? 'line-through text-gray-400' : ''}
  `;

  // Animation variants for card
  const cardVariants = {
    initial: { opacity: 0, scale: 0.96, y: 10 },
    animate: { 
      opacity: 1, 
      scale: 1, 
      y: 0,
      transition: { 
        type: 'spring',
        stiffness: 300,
        damping: 20
      }
    },
    exit: { 
      opacity: 0, 
      x: 60,
      scale: 0.9,
      transition: { 
        duration: 0.2,
        ease: 'easeInOut'
      } 
    },
  };

  return (
    <AnimatePresence onExitComplete={() => {}}>
      <motion.div
        layout
        ref={setNodeRef}
        style={style}
        className={cardClasses.replace('border-l-4', '')}
        variants={cardVariants}
        initial="initial"
        animate={isRemoving ? 'exit' : 'animate'}
        exit="exit"
        transition={{
          type: 'spring',
          stiffness: 300,
          damping: 25,
          mass: 0.5
        }}
      >
        <div className="flex items-center w-full">
          {/* Checkbox */}
          <div className="flex items-center justify-center mr-4 flex-shrink-0">
            <motion.button
              onClick={handleCheckboxClick}
              className={`w-6 h-6 rounded-full border-2 ${
                item.completed ? 'bg-green-500 border-green-500' : 'border-gray-300 hover:border-blue-500'
              } flex-shrink-0 flex items-center justify-center transition-colors duration-200`}
              aria-label={item.completed ? 'Mark as incomplete' : 'Mark as complete'}
              whileTap={{ scale: 0.8 }}
            >
              {item.completed && (
                <svg
                  className="w-4 h-4 text-white"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M5 13l4 4L19 7"
                  />
                </svg>
              )}
            </motion.button>
          </div>

          {/* Task details */}
          <div className="flex-1 min-w-0">
            <div className="flex items-center justify-between">
              <h3 className={titleClasses}>
                {item.title}
              </h3>
              <div className="flex items-center space-x-2">
                {item.time && (
                  <span className="text-sm text-gray-500 whitespace-nowrap">
                    {item.time}
                  </span>
                )}
                {item.duration && (
                  <span className="text-xs px-2 py-1 bg-blue-100 text-blue-800 rounded-full">
                    {item.duration}
                  </span>
                )}
              </div>
            </div>
            {item.description && (
              <p className="text-sm text-gray-600 mt-1 truncate">
                {item.description}
              </p>
            )}
          </div>

          {/* Action buttons */}
          <div className="flex items-center ml-4">
            <button
              onClick={handleRemoveClick}
              className="p-1 rounded-full hover:bg-gray-200 text-gray-500 hover:text-red-500 transition-colors duration-200"
              aria-label="Remove item"
            >
              <svg
                width="20"
                height="20"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              >
                <line x1="18" y1="6" x2="6" y2="18"></line>
                <line x1="6" y1="6" x2="18" y2="18"></line>
              </svg>
            </button>

            <span
              {...attributes}
              {...listeners}
              className="drag-handle flex items-center justify-center rounded-md ml-2 flex-shrink-0"
              style={{
                cursor: 'grab',
                width: 36,
                height: 36,
                touchAction: 'none',
                background: 'none',
              }}
              aria-label="Drag to reorder"
              tabIndex={0}
            >
              <svg
                width="20"
                height="20"
                viewBox="0 0 16 16"
                xmlns="http://www.w3.org/2000/svg"
                fill="#6B7280"
              >
                <path d="M9.5 13a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0zm0-5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0zm0-5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0z" />
              </svg>
            </span>
          </div>
        </div>
      </motion.div>
    </AnimatePresence>
  );
});

export default FocusItemCard;
