// src/components/WeeklyTaskSummary.js
import React from 'react';

/**
 * A single bar representing a day's progress, showing completed and missed tasks.
 */
const DayBar = ({ day, planned, completed, missed, isToday }) => {
  const completedHeight = planned > 0 ? (completed / planned) * 100 : 0;
  const missedHeight = planned > 0 ? (missed / planned) * 100 : 0;

  return (
    <div className="flex flex-col items-center space-y-2">
      <div className="relative">
        <div className="absolute bottom-full mb-2 w-max px-2 py-1 text-xs text-white bg-gray-800 rounded-md opacity-0 group-hover:opacity-100 transition-opacity duration-300">
          {completed}/{planned} Done, {missed} Missed
        </div>
        {/* Bar container */}
        <div className="group h-28 w-6 bg-gray-200 rounded-full flex flex-col justify-end overflow-hidden">
          {/* Missed portion of the bar */}
          <div
            className="bg-red-400 transition-all duration-500"
            style={{ height: `${missedHeight}%` }}
          ></div>
          {/* Completed portion of the bar */}
          <div
            className="bg-cyan-400 transition-all duration-500"
            style={{ height: `${completedHeight}%` }}
          ></div>
        </div>
      </div>
      {/* Day label */}
      <p className={`text-sm font-semibold ${isToday ? 'text-cyan-500' : 'text-gray-500'}`}>
        {day}
      </p>
    </div>
  );
};

/**
 * The main component that displays the weekly summary chart.
 */
const WeeklyTaskSummary = React.memo(({ weeklyData }) => {
  const totalCompleted = weeklyData.reduce((sum, day) => sum + day.completed, 0);
  const totalMissed = weeklyData.reduce((sum, day) => sum + day.missed, 0);

  return (
    <div className="bg-white p-6 rounded-2xl">
      <div className="flex justify-between items-start mb-4">
        <div>
          <h3 className="text-gray-500 text-sm">Overall</h3>
          <p className="text-3xl font-bold text-gray-800">
            {totalCompleted} <span className="text-lg font-medium text-gray-600">Completed</span>
          </p>
          <p className="text-red-500 text-lg font-bold">
            {totalMissed} <span className="font-medium">Missed</span>
          </p>
        </div>
        {/* Updated Legend */}
        <div className="flex flex-col items-end space-y-1 text-sm">
          <div className="flex items-center">
            <span className="w-3 h-3 rounded-full bg-gray-200 mr-2"></span>
            <span>Planned</span>
          </div>
          <div className="flex items-center">
            <span className="w-3 h-3 rounded-full bg-cyan-400 mr-2"></span>
            <span>Completed</span>
          </div>
          <div className="flex items-center">
            <span className="w-3 h-3 rounded-full bg-red-400 mr-2"></span>
            <span>Missed</span>
          </div>
        </div>
      </div>

      <div className="flex justify-around">
        {weeklyData.map((data) => (
          <DayBar
            key={data.day}
            day={data.day}
            planned={data.planned}
            completed={data.completed}
            missed={data.missed}
            isToday={data.isToday}
          />
        ))}
      </div>
    </div>
  );
});

export default WeeklyTaskSummary;
